@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,400;0,600;0,700;1,900&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    padding: 0;
    /* background-color: aquamarine; */
    font-family: 'Poppins', sans-serif;
}

.frost {
    width: auto;
    margin: 0px;
    /* height: auto; */
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .15);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    height: 8vh;
}

.logo {
    padding: 0;
    margin: 0;
    font-weight: 400;
}

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(207, 76, 76, 0.15);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
}

.navbar div {
    display: flex;
    flex-direction: row;
}

.navigation-menu ul {
    display: flex;
    padding: 0;
}

.navbar-menu li {
    list-style-type: none;
    /* horizontal vertical */
    padding: 0px 16px;
}

.navigation-menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
}
.body-container {
    display: flex;
    flex-direction: row;
    color: #000;
    /* take available height */
    height: 90vh;
}

.todoBody {
    width: 100%;
}
